.home-container {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.46), rgba(0, 0, 0, 0.46)), url("../../../public/images/homeHero.png") no-repeat center center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    min-height: 100vh;
    color: white;
    padding: 2.813rem 1.4rem;
    display: flex;
    flex-direction: column;
}

.home-container h1 {
    font-weight: 700;
    font-size: 2.25rem;
    line-height: 2.625rem;
}

.home-container p {
    line-height: 1.5rem;
}

.home-container a {
    display: inline-block;
    text-align: center;
    text-decoration: none;
    background-color: #FF8C38;
    border: none;
    width: 100%;
    margin-top: 1.688rem;
    padding-block: .75rem;
    color: white;
    font-weight: 700;
    border-radius: 0.313rem;
    cursor: pointer;
    transition: transform .1s ease-in-out;
}

.home-container a:hover {
    transform: translate(0.063rem, 0.063rem);
}
